.project-page {
  color: white;
  margin: 120px auto 2vh auto;
  padding: 4vh 5vw;
  background: rgba(0, 0, 0, 0.85);
  border-radius: 20px;
  border: 2px solid #66fcf1;
  box-shadow: 0 0 30px rgba(102, 252, 241, 0.7);
  text-align: center;
  max-width: 1200px;
}

.project-page h1 {
  font-size: clamp(1.8rem, 4vw, 2.8rem);
  font-weight: 700;
  margin: 2vh 0;
  text-shadow: 0 0 1.5rem rgba(102, 252, 241, 1);
  color: #66fcf1;
}

.project-page p {
  font-size: 1.05rem;
  line-height: 1.7;
  max-width: 850px;
  margin: 1.2rem auto;
  color: #e0e0e0;
  text-align: center;
}

.custom-feature-list {
  list-style: none;
  padding: 0;
  margin: 2rem auto;
  text-align: center;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  max-width: 700px;
}

.custom-feature-list li {
  font-size: 1.05rem;
  color: #d1d1d1;
  line-height: 1.6;
}

.tech-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  justify-content: center;
  margin: 4vh 0 2vh 0;
}

.tag {
  background-color: rgba(102, 252, 241, 0.15);
  border: 1px solid #66fcf1;
  padding: 0.6rem 1.2rem;
  border-radius: 2rem;
  font-size: 0.95rem;
  font-weight: 500;
  color: #66fcf1;
  box-shadow: 0 0 0.6rem rgba(102, 252, 241, 0.25);
  transition: transform 0.3s ease;
}

.tag:hover {
  transform: scale(1.05);
}

/* Skills & Contributions Section */
.skills-contributions-wrapper {
  width: 100%;
  display: flex;
  justify-content: center;
  margin: 4vh 0;
  padding: 0 4vw;
  box-sizing: border-box;
}

.skills-contributions-row {
  display: flex;
  flex-direction: row;
  justify-content: center;
  gap: 3vw;
  max-width: 1100px;
  width: 100%;
}

.skills-section,
.contribution-section {
  flex: 1;
  background: rgba(0, 0, 0, 0.7);
  border-radius: 15px;
  border: 2px solid #66fcf1;
  box-shadow: 0 0 20px rgba(102, 252, 241, 0.4);
  text-align: left;
  padding: 2rem;
}

.skills-section h2,
.contribution-section h2 {
  color: #66fcf1;
  margin-bottom: 1.5rem;
  text-align: center;
  text-shadow: 0 0 25px rgba(102, 252, 241, 1);
}

.skills-section ul,
.contribution-section ul {
  list-style-type: disc;
  padding-left: 1.5rem;
}

.skills-section li,
.contribution-section li {
  margin-bottom: 0.75rem;
  color: #d1d1d1;
}

/* Visual Sections (Map / Title Art) */
.gallery-wrapper {
  width: 100%;
  max-width: 1400px;
  margin: 0 auto;
  padding: 4vh 5vw;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  gap: 4vh;
  align-items: center;
}

.gallery-section {
  width: 100%;
  padding: 3vh 4vw;
  background: rgba(0, 0, 0, 0.75);
  border-radius: 1rem;
  border: 2px solid #66fcf1;
  box-shadow: 0 0 2rem rgba(102, 252, 241, 0.5);
  text-align: center;

  /* 👇 Add this */
  margin-bottom: 5vh;
}


.gallery-section h2 {
  color: #66fcf1;
  margin-bottom: 2rem;
  text-shadow: 0 0 25px rgba(102, 252, 241, 1);
}

/* Images */
.project-img {
  width: 100%;
  max-width: 90vw;
  margin: 4vh auto 2vh auto;
  border-radius: 1rem;
  box-shadow: 0 0 2rem rgba(102, 252, 241, 0.5);
  display: block;
}

/* PDF Section */
.pdf-wrapper {
  width: 100%;
  justify-content: center;
  display: flex;
  padding: 3rem 2rem;
  box-sizing: border-box;
}

.pdf-viewer-section {
  width: 100%;
  max-width: 1400px;
  padding: 2.5rem;
  background: rgba(0, 0, 0, 0.75);
  border-radius: 15px;
  border: 2px solid #66fcf1;
  box-shadow: 0 0 30px rgba(5, 6, 6, 0.5);
  text-align: center;
}

.pdf-viewer-section h2 {
  color: #66fcf1;
  margin-bottom: 1.5rem;
  text-shadow: 0 0 25px rgba(102, 252, 241, 1);
}

.pdf-viewer-section iframe {
  width: 100%;
  height: 70vh;
  border: none;
  border-radius: 0.75rem;
  box-shadow: 0 0 2rem rgba(102, 252, 241, 0.5);
}

.project-status-paused {
  color: #ffcc00;
  background: rgba(255, 204, 0, 0.1);
  padding: 0.75rem 1.25rem;
  border: 2px dashed #ffcc00;
  border-radius: 12px;
  font-weight: 600;
  font-size: 1rem;
  display: inline-block;
  margin: 1rem auto 2rem auto;
  box-shadow: 0 0 10px rgba(255, 204, 0, 0.3);
}

/* Mobile Optimizations */
@media (max-width: 900px) {
  .skills-contributions-row {
    flex-direction: column;
    align-items: center;
  }

  .skills-section,
  .contribution-section {
    width: 100%;
    max-width: 600px;
  }

  .project-img {
    max-width: 100%;
  }

  /* Clear, positive “concept only” badge */
.project-status-concept {
  color: #9be8ff;
  background: rgba(155, 232, 255, 0.08);
  padding: 0.75rem 1.25rem;
  border: 2px dashed #9be8ff;
  border-radius: 12px;
  font-weight: 700;
  font-size: 1rem;
  display: inline-block;
  margin: 1rem auto 2rem auto;
  box-shadow: 0 0 10px rgba(155, 232, 255, 0.25);
}

/* Small note under the intro */
.note {
  max-width: 850px;
  margin: 0.5rem auto 1.5rem;
  color: #cfe9f4;
  opacity: 0.95;
  font-size: 0.95rem;
}

}
